.general-btn{
  outline: 0;
  background-color: rgb(19, 148, 255) ;
  color: rgb(255, 255, 255);
  border: 2px solid #0096C2;
  height: 36px;
  line-height: 32px;
  min-width: 130px;
  border-radius: 100px;
  cursor: pointer;
  padding: 0 15px;

  &:hover{
    background-color: #fff;
    color: #0096C2;
  }
}