.form{

  .form-header{
    display: flex;
    justify-content: space-between;
  }
  .form-group{
    width: 100%;
    margin-bottom: 18px;


    label{
      text-transform: uppercase;
      display: block;
      font-size: 12px;
      font-weight: 400;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: .6px;
      color: rgba(0,0,0,.5);
      margin-bottom: 0;
      padding: 0 10px 0 0;
      margin-bottom: 10px;
    }

    input, select{
      width: 100%;
      border-radius: 2px;
      line-height: 16px;
      height: 38px;
      padding: 10px;
      font-size: 13px;
      border: 1px solid rgba(0,0,0,.11);
    }
    textarea{
      height: 41px;
      width: 100%;
      min-height: 80px;
      border-radius: 2px;
      line-height: 16px;
      font-size: 13px;
      padding: 10px;
      border: 1px solid rgba(0,0,0,.11);
    }
  }
  .upload-image{
    .ant-upload-picture-card-wrapper{
      margin-left: 0px;
    }
  }

  .modal-footer{
    padding-top: 15px;
    text-align: right;
  }
}