
.customers{
  background-color: #FAFAFA;

  .customer-header{
    // position: fixed;
    z-index: 8;
    right: 0;
    left: 0;
    width: auto;
    top: 60px;
    margin-bottom: 11px;
    background: #fff;
    height: 57px;
    display: flex;
  
    .search-bar{
      width: 23%;
  
      .fa-search{
        font-size: 20px;
        padding-left: 40px;
      }
  
      input{
        padding-left: 30px;
        font-size: 20px;
        padding-top: 8px;
        background: 0 0;
        border: none;
        outline: 0;
        position: relative;
        margin-left: 0;
        font-weight: 400;
        font-style: normal;
        color: rgba(67, 67, 67, 0.51);
        letter-spacing: 1.5px;
      }
    }
  
    .filter-customer{
      width: 60%;
      background: #fff;
      padding: 14px 22px 14px 13px;
      box-shadow: 0 0 10px rgba(51, 51, 51 / 22%, .4);
      display: flex;
  
  
      .filter-one{
        margin-right: 10px;
      }
  
      .dropdown-filter{ 
  
        .options-select-btn:hover{
          background-color: transparent;
        }
      
        .options-select-btn .option-selected, .options-select-btn .caret-icon{
          border: 1px solid #d6d6d6;
        }
  
        .modal{
          border: 1px solid rgba(0,0,0,.15);
          border-radius: 4px;
          -webkit-box-shadow: 0 6px 12px rgb(0, 0, 0 / 18%);
          box-shadow: 0 6px 12px rgba(0, 0, 0 / 18%, .1);
          margin-top: 0;
        }
      }
    }
    .delete-customer{
      width: 18%;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      padding-right: 22px;
    }
  }

  .customers-table{
    width: 95%;
    margin: 20px auto;
    margin-bottom: 0;

    thead tr {
      background: #fff;

      th{
        background-color: #fff;  
        text-transform: uppercase;
        font-size: 10px;
        font-weight: 600;
        font-style: normal;
        color: rgba(37,45,71,.62);
        border-bottom: 1px solid rgba(0,0,0,.05);
        padding-right: 10px;
        padding-left: 10px;
      }
    }

    tbody{

      tr{
        
        &:hover{
          td{
            background-color: #fff;
            box-shadow: 0 4px 2px -3px rgba(0, 0, 0 / 10%, .1) inset, 0 -4px 2px -3px rgba(0, 0, 0 / 10%, .1) inset;
            opacity: 1 !important;
          }
        }
        &:nth-child(even) {
          background: #fbfbfb;
        }

        td{
          height: 50px;
          padding: 5px 10px 5px 10px;
          color: #000;
          font-family: ProximaNova-Regular,Sans-Serif;
          font-size: 13px;
          font-weight: 300;
          font-style: normal;
          font-stretch: normal;
          line-height: normal;
          letter-spacing: .6px;
          color: rgba(0,0,0,.59);
          border: 0;
          vertical-align: middle;
          border-bottom: 1px solid rgba(132,132,132,.08);
        }
      }    
    }
    
    .ant-table-tbody > tr.ant-table-row-selected > td {
      background: inherit;
      border-color: rgba(0, 0, 0, 0.03);
    }

    .more{
      position: relative;
      padding-left: 10px;

      &:hover .dropdown{
        display: block;
      }

      span{
        color: rgba(0,0,0,.5);
        cursor: pointer;
        font-size: 30px;
        
        &:hover{
          color: #333;
        }
      }

      .dropdown{
        display: none;
        position: absolute;
        top: 26px;
        left: -80px;
        list-style: none;
        text-align: center;
        min-width: 135px;
        margin-top: 2px;
        padding: 5px 0;
        background-color: #fff;
        border: 1px solid rgba(0,0,0,.15);
        border-radius: 4px;
        -webkit-box-shadow: 0 6px 12px rgba(0, 0, 0 / 18%, .1);
        box-shadow: 0 6px 12px rgba(0, 0, 0 / 18%, .1);
        z-index: 1;

        li{
          display: block;
          width: 100%;
          text-align: left;
          padding: 10px 20px;
          height: 35px;
          border-left: 3px solid transparent;
          line-height: 1;
          color: rgba(0,0,0,.73);
          cursor: pointer;
          font-weight: 400;

          &:hover{
            color: #4695f6;
            border-left: 3px solid #4695f6;
            border-radius: 0;
          }
        }
      }
    }
  }
}