.alert{
  position: absolute;
  left: 20px;
  bottom: 20px;
  max-width: 450px;
  width: 100%;

  .ant-alert-error {
    background-color: #323232;
    border: none;
    border-radius: 3px;
    padding: 10px 16px;
    width: 100%;
    letter-spacing: .8px;
  }
  .ant-alert-message{
    color: #FF6A6A;
  }
  .ant-alert-close-icon .anticon-close{
    color: #FF6A6A;
  }
}