.header{
  display: flex;
  align-self: center;
  color: white;
  height: 60px;
  // line-height: 57px;
  background-color: black;
  margin-left: 0;
  width: 100%;
  padding-left: 24px;
  padding-right: 29.5px;


  .menu-button{
    align-self: center;
    margin-right: 15px;
    cursor: pointer;

    div{
      border: 1px solid white;
      border-radius: 5px;
    }
    div:nth-child(1){
      width: 15px;
    }
    div:nth-child(2){
      width: 20px;     
      margin: 4px 0;
    }
    div:nth-child(3){
      width: 15px;
      
    }
  }
  .logo{
    align-self: center;
    height: fit-content;
  }

  .filters{
    align-self: center;
    display: flex;
    font-size: 14px;

    @media (max-width: 969px){
      display: none;
    }
    

    .map-list{
      align-self: center;

      a{
        color: white;
        border: 1px solid rgba(230,230,230,.61);
        padding: 5px 10px;
        font-weight: 500;
        text-decoration: none;
        font-size: 13px;

        &:hover{
          background-color: rgba(158,158,158,.2);
        }
        &.is-active{
          color: black;
          background-color: white;
        }
        .fa{
          margin-right: 10px;
        }
      }
    }
    
    .date-time{
      margin-left: 13px;
    }

    .teams{
      margin-left: 13px;
    }
  }

  .create-task-btn-wrapper{
    font-size: 14px;
    font-family: 'montserrat-light' !important;
    align-self: center;
    margin-left: auto;
    color: black;
    display: flex;
    cursor: pointer;
    min-width: 130px;


    button{
      border: none;
      outline: none;
      border-radius: 100px 0 0 100px;
      border-right: 1px solid black;
      padding-left: 15px;
      padding-right: 7px;
      height: 30px;
      cursor: pointer;
      background-color: #fff;
      font-weight: 500;

    }
    .btn-icon{
      cursor: pointer;
      border-radius: 0 100px 100px 0;
      background-color: white;
      height: 30px;
      display: flex;
      align-items: center;
      width: 30px;
      justify-content: center;
    }
  }

  .header-marketplace{
    align-self: center;
    margin-left: 20px;
    margin-right: 10px;
    margin-top: -10px;

    @media (max-width: 969px){
      display: none;
    }
  }

  .refresh-page{
    align-self: center;
    padding: 7px;
    cursor: pointer;
    
    &:hover{
      background-color: rgba(128,128,128,.57);
    }

    .refresh-page-icon{
      transform: rotate(-70deg);
      font-size: 21px;
    }
    @media (max-width: 969px){
      margin-left: 10px;
    }
  }
  .mobile-filter{
    display: none;

    @media (max-width: 969px){
      display: block;
      align-self: center;
      margin-left: 10px;
    }
  }

}