.dashboard{
  background-color: white;
  overflow: hidden;
  // display: flex;
  // width: 100%;

  .dashboard-task-wrapper{
   transition: margin .3s ease;
    max-width: 370px;
    min-width: 370px;

    &.collapse{
      transition: margin .3s ease;
      margin-left: -370px;
    }
  }
  
  .dashboard-map-wrapper{
    position: relative;
    // flex-grow: 1;
    width: 0;
    

    .collapse-sidebar {
      position: absolute;
      width: 35px;
      height: 39px;
      background: #717171;
      z-index: 1;
      color: #fff;
      cursor: pointer;
      border-radius: 0 0 4px;
      display: flex;
      justify-content: center;
      align-items: center;

      &.right {
        top: 0;
        right: 0;
        border-radius: 0 0 0 4px;
      }
    }
  }

  .dashboard-delegates-wrapper{
    transition: margin .3s ease;
    max-width: 370px;
    min-width: 370px;

 
     &.collapse{
       transition: margin .3s ease;
       margin-right: -370px;
     }
   }
}