.mobile-filter-dropdown{
  .filter-icon{
    font-size: 21px;
  }


  .filter-dropdown{
    position: absolute;
    color: white;
    background-color: #333;
    height: fit-content;
    width: 100%;
    top: 60px;
    left: 0px;
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    .date{
      margin-right: 15px;
      width: fit-content;
    }
    .team{
      width: fit-content;
    }
    .market-place{
      break-after: always;
      page-break-after: always;
    }

  }
}
